
.new-collections{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap:15px;
    margin-bottom: 100px;
    position:relative;
    top:200px;

}
.new-collections h1{
    color: #171717;
    font-size:50px;
    font-weight:600;
}

.new-collections hr{
    width: 200px;
    height: 16px;
    border-radius: 10px;
    background: #252525;
}
.collections{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top:50px;
    gap:30px
}