.popular{
    display:flex;
    flex-direction: column;
    align-items: center;
    gap:15px;

    position:relative;
    top:100px;

}
.popular h1{
    color: #171717;
    font-size:50px;
    font-weight:600;
}

.popular hr{
    width: 200px;
    height: 16px;
    border-radius: 10px;
    background: #252525;
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap: 30px;
   
}